module.exports = "/* Audio */\n\n\n#myTune {\n    display: none;\n}\n\n\n.playmusic-name,\n.playmusic-artist {\n    margin-left: 10px;\n}\n\n\n.tag-playmusic {\n    height: 70px;\n    width: 100%;\n    position: fixed;\n    bottom: 0;\n    left: 0;\n    z-index: 999999998;\n    background-color: black;\n    border-top: 1px solid #6610f2;\n}\n\n\n.playmusic-info {\n    height: 100%;\n    display: inline-block;\n    vertical-align: top;\n    width: 25%;\n    float: left;\n    margin-right: 1%;\n}\n\n\n.imgPlayMusic {\n    display: block;\n    width: 39%;\n    float: left;\n    overflow: hidden;\n    height: 100%;\n    margin-right: 1%;\n    text-align: right;\n}\n\n\n.playmusic-info img {\n    height: 100%;\n    display: block;\n}\n\n\n.playmusic-info .textPlayMusic {\n    display: inline-block;\n    vertical-align: top;\n    margin-top: 10px;\n    width: 60%;\n    float: left;\n}\n\n\n.playmusic-info .textPlayMusic a {\n    display: block;\n    margin-bottom: 5px;\n    overflow: hidden;\n    white-space: nowrap;\n    text-decoration: none;\n    color: white;\n}\n\n\n.playmusic-info .textPlayMusic a:hover {\n    color: #f8f9fa;\n}\n\n\n#playButton .glyphicon-pause {\n    display: none;\n}\n\n\n.playmusic-time {\n    display: inline-block;\n    vertical-align: top;\n    height: 100%;\n    width: 36%;\n    float: left;\n    margin: 0;\n    margin-right: 1%;\n}\n\n\n#playmusic-start {\n    display: inline-block;\n    vertical-align: top;\n    margin-top: 25px;\n    color: #fff;\n    width: 17%;\n    float: left;\n    margin-right: 1%;\n    text-align: center;\n}\n\n\n#playmusic-process {\n    height: 5px;\n/*    borderborder: 1px solid #a00;*/\n    display: inline-block;\n    width: 63%;\n    vertical-align: top;\n    margin-top: 33px;\n    float: left;\n    margin-right: 1%;\n}\n\n\n#playmusic-end {\n    display: inline-block;\n    vertical-align: top;\n    margin-top: 25px;\n    color: #fff;\n    width: 18%;\n    float: left;\n    text-align: center;\n}\n\n\n#playmusic-process {\n    cursor: pointer;\n}\n\n\n#playmusic-process::-webkit-progress-bar {\n    background: #eee;\n}\n\n\n#playmusic-process::-webkit-progress-value {\n    background: #6610f2;\n\n}\n\n\n.playmusic-button {\n    width: 37%;\n    margin: 0;\n    display: inline-block;\n    float: left;\n    height: 100%;\n}\n\n\n#playButton,\n#pauseButton,\n#loopButton,\n#playmusic-prev,\n#playmusic-next,\n#volume {\n    border: 0;\n    font-size: 100%;\n    padding: 2px 5px;\n    background: transparent;\n    color: #888;\n    margin-top: 23px;\n    vertical-align: top;\n    outline: none;\n    display: inline-block;\n    text-align: center;\n}\n\n\n#playmusic-prev,\n#playmusic-next {\n    float: left;\n    width: 12%;\n    margin-left: 0;\n    margin-right: 1%;\n}\n\n\n#playButton {\n    font-size: 180%;\n    color: #fff;\n    margin-top: 14px;\n    width: 15%;\n    margin-right: 1%;\n    float: left;\n}\n\n\n#loopButton {\n    float: left;\n    width: 12%;\n    margin-right: 1%;\n    display: inline-block;\n}\n\n\n#volume {\n    margin-top: 0;\n    color: #fff;\n    font-size: 130%;\n    height: 100%;\n    display: inline-block;\n    padding: 0;\n    width: 45%;\n    float: left;\n    text-align: center;\n}\n\n\n#volumeButton {\n    display: inline-block;\n    vertical-align: top;\n    margin-top: 21px;\n    background: transparent;\n    width: 29%;\n    border: 0;\n    float: left;\n    margin-right: 1%;\n    outline: none;\n    color: aliceblue;\n}\n\n\n#volumeButton .glyphicon-volume-down,\n#volumeButton .glyphicon-volume-off {\n    display: none;\n}\n\n\n#volumeProBar {\n    height: 5px;\n    display: inline-block;\n    vertical-align: top;\n    margin-top: 31px;\n    border: 0px;\n    width: 62%;\n    margin-right: 8%;\n    cursor: pointer;\n}\n\n\n#volumeProBar::-webkit-progress-bar {\n    background: #fff;\n}\n\n\n/* End Audio */\n/*# sourceMappingURL=data:application/json;base64, */"